<template>
  <div>
      <input type="text" placeholder="Procurar..." class="searchTab">
      <div class="refreshButton" @click="reloadData">
          <h3 class="button_label">Atualizar</h3>
          <img class="refreshButton_icon" src="../assets/icons/playerList/refresh.svg" :class="{refreshButton_icon_processing: isProcessing}">
      </div>
      <table class="tableClass">
        <tr>
            <th width="2%" style="user-select: none;">ID</th>
            <th width="10%" style="user-select: none;">Nome</th>
            <th width="14%" style="user-select: none;">Discord</th>
            <th width="auto" style="user-select: none;">Ações</th>
            <th width="auto" style="user-select: none;">Info</th>
            <th width="10%" style="user-select: none;">Latençia</th>
        </tr>
        <tr class="trRow" v-for="(player, index) of players" :key="index">
            <th style="font-size:.7vw">{{player.id}}</th>
            <th style="font-size:.7vw">{{player.name}}</th>
            <th><img class="mini_discordImg" :class="{isOnline: player.isOnline, isOffline: !player.isOnline}" :src='httpsURL + "cdn.discordapp.com/avatars/" + player.discord.id + "/" + player.discord.avatar'> <h3 class="mini_discord_label"> {{player.discord.name}}#{{player.discord.discriminator}} </h3></th>
            <th width="60%">
                <actionImageButton
                    :label="'Banir'"
                    :img="'ban'"
                    @buttonclick="''"
                ></actionImageButton>

                <actionImageButton
                    :class="{buttonDisabled: !player.isOnline}"
                    :label="'Expulsar'"
                    :img="'kick'"
                    @buttonclick="''"
                ></actionImageButton>

                <actionImageButton
                    :label="'Notificação'"
                    :img="'notification'"
                    @buttonclick="''"
                ></actionImageButton>

                <actionImageButton
                    :label="mutePlayerLabel(index)"
                    :img="isPlayerMuted(player.isMuted)"
                    @buttonclick="mutePlayer(index, $event)"
                ></actionImageButton>

                <actionImageButton
                    :class="{buttonDisabled: !player.isOnline}"
                    :label="'Teleportar'"
                    :img="'teleport'"
                    @buttonclick="''"
                ></actionImageButton>

            </th>
            <th @click="openMoreInfo(index)"><img class="mini_icon setContrast" style="transform: rotateZ(90deg)" src="../assets/icons/playerList/more.svg"></th>
            <th>
                <img class="mini_icon" src="../assets/icons/playerList/high-signal.svg" v-show="player.ping < 50 && player.isOnline">
                <img class="mini_icon" src="../assets/icons/playerList/mid-signal.svg" v-show="player.ping <= 100 && player.ping >= 50 && player.isOnline">
                <img class="mini_icon" src="../assets/icons/playerList/low-signal.svg" v-show="player.ping > 100 && player.ping <= 1000 && player.isOnline">
                <img class="mini_icon" src="../assets/icons/playerList/no-signal.svg" v-show="player.ping > 1000 || !player.isOnline">
                <h5 class="ping_label" v-show="player.ping !== null">{{player.ping}}</h5>
            </th>
        </tr>  
      </table>
      <transition name="fade">
        <div v-show="openMoreInfoTab" class="more_info_tab" @focus="openMoreInfoTab = !openMoreInfoTab">
            <playerMoreInfo 
            :charID="chachedCharId"
            @buttonclick="openMoreInfoTab = !openMoreInfoTab"
            ></playerMoreInfo>
        </div>
      </transition>
  </div>
</template>

<script>
import actionImageButton from './buttons/actionImageButton.vue'
import playerMoreInfo from './playerMoreInfo.vue'
export default {
    components: {
        actionImageButton,
        playerMoreInfo
    },

    data() {
        return {
            httpsURL: 'https://',
            openMoreInfoTab: false,
            isProcessing: false,

            //Char ID for more info
            chachedCharId: null,
            players: [
                {"id":18,"name":"Roberto Alves","discord":{"id":"215253285207146496","name":"MalvadoEZY","discriminator":"8845","avatar":"44ecb06cc7a769bd0b4da567862c2e25"},"ping":51,"isMuted":false,"isOnline":true}
            ]
        }
    },
    methods: {
        loadPlayers: function(loadedPlayers) {
            this.players = loadedPlayers
            this.isProcessing = false
        },
        isPlayerMuted: function(isMuted) {
            if(isMuted) {
                return 'mute';
            } else {
                return 'unmute';
            }
        },

        reloadData: function () {
            this.isProcessing = true
            setTimeout(() => {
                this.isProcessing = false
            }, 5000);
            alt.emit('adminmenu:reloadplayer')
        },

        mutePlayer: function(index) {
           this.players[index].isMuted = !this.players[index].isMuted
        },

        mutePlayerLabel: function(index) {
            if(this.players[index].isMuted) {
                return 'Ativar chat'
            } else {
                return 'Desativar chat'
            }
        },

        openMoreInfo: function(index) {
            if(this.openMoreInfoTab) {
                this.chachedCharId = this.player.id
                this.openMoreInfoTab = false;
                return;
            } else {
                this.openMoreInfoTab = true
                //alt.emit() emit request
            }
        },

        loadPlayers: function (playerarray) {
            this.players = playerarray
            this.$emit('playerListLoaded')
        }
    },
    mounted() {
        if('alt' in window) {
            setTimeout(() => {
                alt.emit('adminmenu:ready')
            }, 50);
        }
        
        alt.on('adminmenu:start', this.loadPlayers)
    },
}
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: transform .2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: scale(0);
}

.more_info_tab {
    position: absolute;
    left: 10%;
    top: 10%;
    width: 80%;
    height: 80%;
    background-color: #2e313a;
    border-radius: 10px;
    z-index: 3;
}


.buttonDisabled {
    filter: blur(2px) contrast(.5);
}
.setContrast {
    user-select: none;
    transition: .4s;
    filter: contrast(.5);
}

.setContrast:hover {
    filter: contrast(1);
}

.mini_icon {
    position: relative;
    user-select: none;
    transform: translate(50%, 5%);
    margin-top: 20%;
    width: 2vw;
    padding: 1%;
    margin: 0%;
}

.ping_label {
    position: absolute;
    user-select: none;
    font-family: Opensans-Regular;
    transform: translate(0%, -70%);
    font-size: .8vw;
    margin: 1.5vw;
    right: 5%;
    text-align: left;
    display: inline-block;
}
.trRow{
    transition: .4s;
}
.trRow:hover {
    background-color: #84e37721;
}



.moreinfo_lowertable {
    position: absolute;
    z-index: 5;
    height: 50%;
    width: 100%;
    background-color: #080808;
}

.mini_discordImg {
    padding: 1%;
    width: 1.6vw;
    border-radius: 50%;
    transform: translate(0.5vw, .5vw);
    user-select: none;
}

.isOnline {
    background-color: rgb(113, 239, 128);
}

.isOffline {
    background-color: rgb(239, 124, 113);
}

.mini_discord_label {
    position: relative;
    user-select: text;
    display: inline-block;
    width: 50%;
    text-align: center;
    transform: translate(1vw, 0vw);
    padding: 0%;
    font-size: .6vw;
}

.searchTab {
    position: absolute;
    user-select: none;
    color:white;
    background-color: rgba(0, 0, 0, 0.349);
    outline: 0;
    border: 0;
    border-radius: 10px 10px 5px 5px;
    border-bottom: 2px solid white;
    width:46%;
    top: 2%;
    left: 2%;
    height: auto;
    font-size: 1vw;
    padding: 1%;
    z-index: 2;
}

.refreshButton {
    position: absolute;
    user-select: none;
    width: 10%;
    height: auto;
    font-size: 1vw;
    padding: 1%;
    left: 54%;
    top: 2%;
    text-align: left;
    color: white;
    background-color: rgb(98, 113, 133);
    border-radius: 5px;
    transition: .5s;
    z-index: 2;
}

.refreshButton:hover {
    background-color: rgba(101, 116, 151, 0.466);
}

.button_label {
    font-size: 1vw;
    padding: 0%;
    margin: 0%;
    user-select: none;
    pointer-events: none;
}

.refreshButton_icon {
    position: absolute;
    top: 15%;
    right: 10%;
    width: 1.6vw;

}

.refreshButton_icon_processing {
    transform: translateY(0px);
    animation: 1s rotating_icon infinite;
}

@keyframes rotating_icon {
    from{transform: rotateZ(0deg);}
    to{transform: rotateZ(360deg);}
}

.tableClass {
    position: absolute;
    overflow-y: scroll;
    padding: 2%;
    width: 100%;
    top: 10%;
    color: white;
    background-color: #26282e;
}

td, tr {
    border-bottom: 2px solid rgb(233, 240, 255);
}

table {
    margin-left: auto; 
    margin-right: auto; 
    font-size: .8vw; 
    width: 50%; 
    table-layout: auto;
    
}

table td > div {
    overflow: hidden;
}
</style>