        <headerButton :label="'Servidor'" @buttonClicked="currentPage = 1" :class="{pageSelected: currentPage === 1}"></headerButton>
        <headerButton :label="'Logs'" @buttonClicked="currentPage = 2" :class="{pageSelected: currentPage === 2}"></headerButton>
        <headerButton :label="'Tickets'" @buttonClicked="currentPage = 3" :class="{pageSelected: currentPage === 3}"></headerButton> 
