<style scoped>

.pageSelected {
    background-color: rgba(0, 68, 255, 0.411);
}

.fade-enter-active {
  transition: opacity .2s ;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.header {
    position: relative;
    top: 0%;
    width: 100%;
    height: 15%;
    padding: 0%;
    margin: 0%;
    background-color: #2e313a;
    border-bottom: 4px solid rgb(63, 63, 63);
    overflow: hidden;
}

.container {
    position: absolute;
    overflow-y: scroll;
    background-color: #26282e;
    top: 13%;
    width: 100%;
    height: 87%;
}

.container::-webkit-scrollbar {
    background-color: #26282e;
}

.container::-webkit-scrollbar-thumb {
    background-color: #0077b6;
}

.container::-webkit-scrollbar-button{
    background-color: #26282e;
}


.sk-chase {
    width: 10vw;
    height: 10vw;
    left: 35vw;
    top: 20vw;
    position: absolute;
    animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
