<template>
    <div id="icon_container" @click="buttonPressed" @mouseover="buttonHover = true" @mouseout="buttonHover = false">
        <img class="mini_icon" :class="{iconSelected: buttonHover, notIconSelected: !buttonHover}" :src="require('../../assets/icons/playerList/' + img + '.svg')">
        <h3 class="mini_icon_label">{{label}}</h3>
    </div>
</template>

<script>
export default {
    props: {
        img: String,
        label: String,
        color: String
    },
    data() {
        return {
            buttonHover: false
        }
    },
    methods: {
        buttonPressed: function(e) {
            this.$emit('buttonclick', e)    
        }
    },
}
</script>

<style scoped>
.iconSelected {
    filter: contrast(1);
    transition: .5s;
}

.notIconSelected {
    filter: contrast(.3);
    transition: .5s;
}

#icon_container {
    position: relative;
    display: inline-block;
    border: 1px solid #3f3f3f;
    background-color: rgb(34, 36, 42);
    border-radius: .5vw;
    transition: .2s;
    margin: 0% .5% 0% .5%;
    padding-left: 5%;
    padding-right: 1%;
    height: auto;
    width: 11%;
    transition: .5s;
}

#icon_container:hover {
    background-color: rgb(15, 15, 15);
}

.mini_icon {
    position: absolute;
    left: 5%;
    top: 15%;
    width: 1.5vw;
    pointer-events: none;
    user-select: none;
}



.mini_icon_label {
    position: relative;
    user-select: none;
    font-size: .7vw;
    right: 2%;
    display: inline-block;
}
</style>